import React, { useState } from 'react'
import queryString from 'query-string'
import { useLocation } from 'react-router-dom'
import { observer } from 'mobx-react-lite'

import mainStyles from './styles/main.module.css'
import lightStyles from './styles/light.module.css'
import darkStyles from './styles/dark.module.css'

const light = { ...mainStyles, ...lightStyles }
    , dark = { ...mainStyles, ...darkStyles }

import store from './../../store'

import Cards from './components/tapes/cards'
import Filter from './components/filter'
import SmallCards from './components/tapes/small-cards'
import Footer from './components/footer'

import useScrollToTop from './../../lib/use-scroll-to-top'
import useCastumStyle from './../../lib/use-castum-style'

import useHead from 'generatamine/libs/react'
import header from './../../headers/projects'

import servicesIcon from './../../assets/icons/service.png'
import merchIcon from './../../assets/icons/merch.png'

import mainItems from './../../main-items.js'

const otherItems = [{
  image: servicesIcon,
  title: ' ',
  description: '        ...',
  link: ['/services', ''],
}, {
  image: merchIcon,
  title: '',
  description: '     .',
  link: ['/merch', ''],
}, ...mainItems]

const Projects = observer(() => {
  useScrollToTop()
  useCastumStyle()

  useHead(
    header()
  )

  const styles = store.theme === 'light'
                  ? light
                  : dark

  const location = useLocation()
  const query = queryString.parse(location.search)
  const [tag, setTag] = useState(query.tag || '')

  const items = store.api.projects || []

  const tags = Object.keys(
    items.map(({ tags }) =>
      tags.map(tags => tags)
    ).flat().reduce((ctx, tag) => {
      ctx[tag] = ''
      return ctx
    }, {})
  )

  const _items = items.filter(item => tag !== '' ? item.tags.find(_tag => _tag === tag) : true)

  return (
    <div className={styles.wrapper}>
      <h1 className={styles.titleH1 + ' ' + styles.titleMarginTop}> </h1>
      <p className={styles.description}  dangerouslySetInnerHTML={{ __html: '   : <selection>     </selection>,       .       ,   : <b>  </b>.              ,   (   )       <selection></selection>.' }} />
      <p className={styles.quote}>    ,      ,     <b> </b>  <a className={styles.link} href='https://www.donationalerts.com/r/prohetamine'> </a>.</p>
      <Filter tags={tags} activeTag={tag} onTag={tag => setTag(tag)} />
      <Cards items={_items} path='/project' />
      <h1 className={styles.titleH1}>- </h1>
      <SmallCards items={otherItems} />
      <Footer />
    </div>
  )
})

export default Projects
