import React from 'react'
import { observer } from 'mobx-react-lite'

import mainStyles from './styles/main.module.css'
import lightStyles from './styles/light.module.css'
import darkStyles from './styles/dark.module.css'

const light = { ...mainStyles, ...lightStyles }
    , dark = { ...mainStyles, ...darkStyles }

import store from './../../store'

import Sound from './components/tapes/sound'
import SmallCards from './components/tapes/small-cards'
import Footer from './components/footer'

import useScrollToTop from './../../lib/use-scroll-to-top'
import useCastumStyle from './../../lib/use-castum-style'

import useHead from 'generatamine/libs/react'
import header from './../../headers/history'

import myProgressIcon from './../../assets/icons/my_progress.png'
import articlesIcon from './../../assets/icons/articles.png'

import mainItems from './../../main-items.js'

const otherItems = [{
  image: myProgressIcon,
  title: ' ',
  description: '          . ...',
  link: ['/achievements', ''],
}, {
  image: articlesIcon,
  title: '',
  description: '   .    ,    ...',
  link: ['/articles', ''],
}, ...mainItems]

const HistorySound = observer(() => {
  useScrollToTop()
  useCastumStyle()
  useHead(
    header()
  )

  const styles = store.theme === 'light'
                  ? light
                  : dark

  return (
    <div className={styles.wrapper}>
      <h1 className={styles.titleH1 + ' ' + styles.titleMarginTop}> </h1>
      <p className={styles.description}>   . <b></b>    ,       ,     ,     ,                    .   .     .</p>
      <Sound items={store.api.historySound || []} />
      <h1 className={styles.titleH1}>- </h1>
      <SmallCards items={otherItems} />
      <Footer />
    </div>
  )
})

export default HistorySound
