import React, { useEffect, useState } from 'react'
import { observer } from 'mobx-react-lite'
import { Player } from '@lottiefiles/react-lottie-player'
import useScrollPosition from '@react-hook/window-scroll'
import { isBrowser } from 'react-device-detect'

import lightStyles from './styles/light.module.css'
import darkStyles from './styles/dark.module.css'

import store from './../../store'

import Radio from './components/radio'
//import Image from './components/image'
//import Video from './components/video'
import SmallCards from './components/tapes/small-cards'
import Footer from './components/footer'

import useScrollToTop from './../../lib/use-scroll-to-top'
import useCastumStyle from './../../lib/use-castum-style'

import useHead from 'generatamine/libs/react'
import header from './../../headers/lenta'

import projectsIcon from './../../assets/icons/projects.png'
import servicesIcon from './../../assets/icons/service.png'

import mainItems from './../../main-items.js'

const otherItems = [{
  image: projectsIcon,
  title: ' ',
  description: '   :     ...',
  link: ['/projects', ''],
}, {
  image: servicesIcon,
  title: ' ',
  description: '        ...',
  link: ['/services', ''],
}, ...mainItems]

const Lenta = observer(() => {
  useScrollToTop()
  useCastumStyle()
  useHead(header())

  const styles = store.theme === 'light'
                  ? lightStyles
                  : darkStyles

  const scrollY = useScrollPosition(10)

  const [followMouse, setFollowMouse] = useState(false)
  const [mouse, setMouse] = useState({ x: 0, y: 0 })

  useEffect(() => {
    if (scrollY > 200) {
      setFollowMouse(true)
    } else {
      setFollowMouse(false)
    }
  }, [scrollY])

  useEffect(() => {
    let timeid = null

    const handler = (e) => {
      clearTimeout(timeid)
      timeid = setTimeout(() => {
        setMouse({ x: e.pageX, y: e.pageY })
      }, 500)
    }

    window.addEventListener('mousemove', handler)
    window.addEventListener('scroll', handler)
    return () => {
      window.removeEventListener('scroll', handler)
      window.removeEventListener('mousemove', handler)
    }
  }, [])

  return (
    <div className={styles.wrapper}>
      {
        <Player
          autoplay
          loop
          mode='normal'
          speed='1'
          src='/api/media/sticker.json'
          style={{
            position: 'relative',
            zIndex: 99999999999,
            padding: '40px',
            width: '12vw',
            minWidth: '140px',
            height: '12vw',
            minHeight: '140px',
            transition: followMouse ? `3s` : `0.8s`,
            transform: (isBrowser && followMouse) ? `translate(${mouse.x - 500}px, ${mouse.y}px) scale(0.8)` : 'translate(0px, 0px) scale(1)'
          }}
        />
      }
      <h1 className={styles.titleH1}> </h1>
      <p className={styles.description}>, , , ,     ().</p>
      {/*
        <h2 className={styles.titleH2} name='uran'>  </h2>
        <p className={styles.description}> <b>13  2022 </b>.  <b> </b>       -      -,     - , , .        ,          .          ,     .     <selection>IT </selection>       .</p>
        <p className={styles.quote}>  ,      .</p>
        <p className={styles.description}>  <b> :</b></p>
        <Video src='/api/video/asdasdasdasdasdasdadsedwedw232.mp4' />
        <h2 className={styles.titleH2} name='uran'></h2>
        <p className={styles.description}>    <u><b>   </b></u>.    <b>90%</b>           .   <b>8  2022 </b>    <selection>3500$</selection>.      <a href='https://t.me/prohetamine'></a>   <a href='/#social'> </a>.      .</p>
        <Image alt='' src='/api/images/1638256412121957268.jpg' />
      */}
      <h2 className={styles.titleH2} name='radio-ether'> </h2>
      <p className={styles.description}>-  ,  <b>5  2022 </b>       .        ,   ! <a href='https://prohetamine.github.io/radio-boomer/'>Radio.Boomer FM</a>.           \_()_/</p>
      <Radio />
      <h1 className={styles.titleH1}>- </h1>
      <SmallCards items={otherItems} />
      <Footer />
    </div>
  )
})

export default Lenta
